.App {
  text-align: center;
  width:480px;
  margin: 0 auto;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.CardWrapper 
{
  display: inline-flex;
}
.resetButton {
    color: #fff;
    background-color: #7952b3;
    border-color: #7952b3;
    padding: .8rem 2rem;
    font-weight: 600;
    cursor: pointer;
    border:none;
    border-radius: 3px;
}
.resetButton:hover {
  background-color: #61428f;
  border-color: #61428f;
  cursor: pointer;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
